body {
  height: 100vh;
  width: 100vw;
  font-family: 'Raleway', sans-serif;
  background: url('https://images.pexels.com/photos/938965/pexels-photo-938965.jpeg?auto=compress&cs=tinysrgb&h=750&w=1260')
    no-repeat;
  background-size: cover;
  font-weight: 600;
}

i,
button {
  cursor: pointer;
}

.app {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(230, 230, 230, 0.7);
  z-index: 0;
}
input,
input:focus,
input:active {
  border: none;
  outline: none;
  background-color: transparent;
}

.toDoContainer {
  min-height: 80vh;
  width: 30vw;
  margin: auto;
  z-index: 1;
}

/* TODOHEADER STYLES */
.toDoHeader {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  text-align: center;
  width: 100%;
  padding: 1em 0;
}

.toDoHeader .inputContainer {
  width: 18vw;
  position: relative;
}

.toDoHeader .errorText {
  font-size: 0.8rem;
  color: #dd0000;
  position: absolute;
  text-align: center;
  top: 35%;
  left: 19%;
}

@media only screen and (min-width: 764px) and (max-width: 1400px) {
  .toDoHeader .errorText {
    top: 27%;
    right: 17%;
  }
}

.toDoContainer .toDoHeader .inputContainer input {
  width: 15vw;
  border: none;
  border-bottom: 1px solid #848484;
}

.toDoContainer .toDoHeader .inputContainer .description {
  margin-bottom: 1em;
}

.toDoContainer .toDoHeader .inputContainer i {
  margin: 0 0.5em;
}

.toDoContainer .toDoHeader .inputContainer button {
  margin-top: 2.8em;
  border: none;
  background: #1db954;
  color: #eee;
  padding: 0.5em;
  border-radius: 50px;
  min-width: 7.5vw;
  font-weight: 600;
}

/* TODOLIST STYLES*/
.toDoList {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  height: 60vh;
}

.toDoList h3 {
  text-align: center;
}

.toDoList .listContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 28vw;
  height: 50vh;
  overflow-y: auto;
  padding: 0.5em;
}

/* TODO STYLES */
.toDo {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  width: 100%;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin: 0.5em 0;
  min-height: 8vh;
  -webkit-animation: appearAnim 0.28s;
          animation: appearAnim 0.28s;
  opacity: 1;
}

.toDo p {
  width: 24vw;
  cursor: pointer;
}

.toDo .description {
  margin: -0.6em 0 0 1.2em;
  color: #848484;
  -webkit-animation: appearAnim 0.28s;
          animation: appearAnim 0.28s;
  opacity: 1;
}

@-webkit-keyframes appearAnim {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes appearAnim {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.toDo .done {
  text-decoration: line-through;
}

.toDo i {
  margin: 0 0.2em;
}

.toDo .fa-times {
  color: #dd0000;
}
/* TODOSEARCH STYLES */

.toDoSearch {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 1em;
}

.searchContainer {
  border-bottom: 1px solid #848484;
}

.searchContainer i,
.searchContainer input {
  margin-bottom: 0.2em;
}

.searchContainer i {
  margin-right: 0.2em;
  color: #848484;
}

@media only screen and (min-width: 320px) and (max-width: 760px) {
  body {
    max-height: 100vh;
  }

  .overlay {
    height: 100vh;
  }
  .toDoContainer {
    width: 100vw;
  }
  .toDoHeader .inputContainer {
    width: 40vw;
  }

  .toDoHeader .errorText {
    font-size: 0.68rem;
    top: 30%;
    left: 0;
  }

  .toDoContainer .toDoHeader .inputContainer input {
    width: 30vw;
  }

  .toDoList .listContainer {
    width: 80vw;
  }

  .toDo p {
    width: 60vw;
    cursor: pointer;
  }

  .toDoSearch {
    padding: 0;
    margin-top: -1.2em;
  }
}



